export function isSelector(x) {
    return typeof x === 'string' && x.length > 1;
}
export function ensureElementsAll(selectorElement, context = document) {
    if (isSelector(selectorElement)) {
        return Array.from(context.querySelectorAll(selectorElement));
    }
    if (selectorElement instanceof NodeList) {
        return Array.from(selectorElement);
    }
    if (Array.isArray(selectorElement)) {
        return selectorElement;
    }
    throw new Error(`Unknown selector element`);
}
export function ensureElement(selectorElement, context) {
    if (isSelector(selectorElement)) {
        const elements = ensureElementsAll(selectorElement, context);
        if (elements.length > 1) {
            console.warn(`selector ${selectorElement} return more then one element`);
        }
        if (elements.length === 0) {
            throw new Error(`selector ${selectorElement} return nothing`);
        }
        return elements.pop();
    }
    if (selectorElement instanceof HTMLElement) {
        return selectorElement;
    }
    throw new Error('Unknown selector element');
}
export function cloneTemplate(query) {
    const template = ensureElement(query);
    return template.content.firstElementChild.cloneNode(true);
}
export function isBoolean(v) {
    return typeof v === 'boolean';
}
export function totalPrice(price) {
    if (price === null) {
        return 'Бесценно'; // Обработка случая, когда цена равна null
    }
    const priceToString = price.toString();
    return priceToString.length < 5
        ? priceToString
        : priceToString
            .split('')
            .reverse()
            .map((item, index) => (index % 3 === 2 && index !== priceToString.length - 1) ? item + ' ' : item)
            .reverse()
            .join('');
}
