import { totalPrice } from '../utils/utils';
import { Component } from './base/Components';
// Класс, описывающий корзину товаров
export class Basket extends Component {
    constructor(blockName, container, events) {
        super(container);
        this.blockName = blockName;
        this.events = events;
        this._button = container.querySelector(`.${blockName}__button`);
        this._price = container.querySelector(`.${blockName}__price`);
        this._list = container.querySelector(`.${blockName}__list`);
        if (this._button) {
            this._button.addEventListener('click', () => this.events.emit('basket:order'));
        }
    }
    // Сеттер для общей цены
    set price(price) {
        this._price.textContent = totalPrice(price) + ' синапсов';
    }
    // Сеттер для товаров
    set list(items) {
        this._list.replaceChildren(...items);
        this._button.disabled = items.length ? false : true;
    }
    // Метод отключающий кнопку "Оформить"
    offButtonOrders() {
        this._button.disabled = true;
    }
    // Метод для обновления индексов таблички при удалении товара из корзины
    resetIndex() {
        Array.from(this._list.children).forEach((item, index) => {
            const indexElement = item.querySelector(`.basket__item-index`);
            if (indexElement) {
                indexElement.textContent = (index + 1).toString();
            }
        });
    }
}
export class BasketItemStore extends Component {
    constructor(blockName, container, actions) {
        super(container);
        this.blockName = blockName;
        this._title = container.querySelector(`.${blockName}__title`);
        this._index = container.querySelector(`.basket__item-index`);
        this._price = container.querySelector(`.${blockName}__price`);
        this._button = container.querySelector(`.${blockName}__button`);
        if (this._button) {
            this._button.addEventListener('click', (evt) => {
                this.container.remove();
                actions === null || actions === void 0 ? void 0 : actions.onClick(evt);
            });
        }
    }
    set title(value) {
        this._title.textContent = value;
    }
    set index(value) {
        this._index.textContent = value.toString();
    }
    set price(value) {
        this._price.textContent = totalPrice(value) + ' синапсов';
    }
}
